@use '../abstracts/constants';

.dark-view {
    position: fixed;
    top: 0;
    left: 0;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: rgb(128 128 128 / 57.5%);
}

.modal {
    position: relative;
    width: 450px;
    margin: 0 auto;
    padding: 20px;
    border-radius: 20px;
    background-color: constants.$color-body;
}

.icon-close {
    cursor: pointer;
    position: absolute;
    top: 12px;
    right: 12px;
}

.modal-content {
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 25px;
}

.textarea {
    resize: none;
    width: 90%;
    height: 100px;
    border-radius: 5px;
    background-color: constants.$color-option-field;
}

.modal-buttons {
    display: flex;
    gap: 10px;
}

.modal-buttons button {
    padding: 6px 12px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    color: constants.$color-title-text;
    background-color: #6f7099;
}

.modal-message {
    padding: 20px;
    font-family: Roboto, sans-serif;
}
