@use '../abstracts/constants';

.decision-menu {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    width: 65%;
}

.top-elements {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.time-label {
    margin-left: 10px;
    font-size: 18px;
}

.input-time {
    margin-left: 6px;
    padding: 6px;
    border-radius: 5px;
    font-size: 20px;
    background-color: constants.$color-option-field;
}

.decision-back {
    width: 35%;
    border: none;
    border-radius: 5px;
    font-family: Roboto, sans-serif;
    font-size: 18px;
    background-color: constants.$color-option-buttons;
}

.decision-pick {
    width: 100%;
    padding: 6px 12px;
    border: none;
    border-radius: 5px;
    font-family: Roboto, sans-serif;
    font-size: 20px;
    color: constants.$color-title-text;
    background-color: constants.$color-button-pick;
}

.input-final-option {
    width: 80%;
    padding: 8px 12px;
    border: none;
    border-radius: 11px;
    font-size: 20px;
}
